.container {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 161px;
  height: 100%;
  color: white;
  cursor: pointer;
  user-select: none;

  .photo {
    margin: 0 10px;
    border-radius: 100%;
    height: 40px;
    width: 40px;
    background-size: cover;
  }

  .userName {
    position: relative;

    &::before {
      content: '';
      background-color: white;
      bottom: -3px;
      left: 0;
      position: absolute;
      width: 100%;
      height: 1.5px;
      transition: all 0.1s ease;
      transform: scaleX(0);
    }

    &.active::before {
      transform: scaleX(1);
    }
  }

  .arrow {
    margin: 2px 10px 0 10px;
    transform: rotate(90deg);
    background-size: cover;
    height: 10px;
    width: 10px;
  }
}
